.button {
  @include link-reset;
  @include button-reset;
  position: relative;
  padding: 1rem 2rem;
  overflow: hidden;
  font-size: 16px;
  text-align: center;
  text-transform: uppercase;
  color: $button;
  border: 1px solid $button-border;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(
      120deg,
      transparent,
      $button-shadow,
      transparent
    );
    transition: all 650ms;
  }

  &:hover,
  &:focus {
    box-shadow: 1px 1px 25px 10px $button-shadow;

    &::before {
      left: 100%;
    }
  }

  &:active {
    color: $button-active;
    text-shadow: $button-shadow;
    border: 1px solid $button-border-active;
  }

  &--menu {
    font-size: 20px;
    color: $menu-button;
    text-shadow: 2px 4px 5px $menu-button-text-shadow;
    background-color: $menu-button-back;
    border: 2px solid $title;

    &::before {
      background: linear-gradient(
        120deg,
        transparent,
        $menu-button-shadow,
        transparent
      );
    }

    &:hover,
    &:focus {
      box-shadow: 1px 1px 20px 10px $menu-button-shadow;
    }

    &:active {
      color: $menu-button-shadow;
      text-shadow: 2px 4px 5px $menu-button-active;
      border: 2px solid $title-shadow;
    }
  }

  &--demo,
  &--details {
    padding: 0.5rem 1rem;
  }

  &--git {
    padding: 0.3rem;
    font-size: 22px;
  }

  &--details {
    color: $main-text;
    border-color: $main-border;

    &::before {
      background: linear-gradient(
        120deg,
        transparent,
        $main-shadow,
        transparent
      );
    }

    &:hover,
    &:focus {
      box-shadow: 1px 1px 25px 10px $main-shadow;
    }

    &:active {
      color: $main-active;
      text-shadow: $main-shadow;
      border: 1px solid $main-active;
    }
  }

}










