.card {
  position: relative;
  width: 450px;
  height: 300px;
  font-family: "Arial", sans-serif;
  transition: all 1s ease;
  box-shadow: -.1rem 1.7rem 6.6rem -3.2rem rgba(0,0,0,0.5);
  opacity: 0.9;

  &--typoteka {
    .card__inside {
      background-color: $typoteka-project;
      color: $nerds-project-text;
    }
  }

  &--avtomoto {
    .card__inside {
      background-color: $avtomoto-project;
      color: $avtomoto-project-text;

      strong {
        color: $avtomoto-project-strong;
      }
    }
  }

  &--ligabank,
  &--ligabankcredit {
    .card__inside {
      background-color: $liga-project;
      color: $liga-project-text;

      strong {
        color: $liga-project-strong;
      }
    }
  }

  &--wtw {
    .card__inside {
      background-color: $wtw-project;
      color: $wtw-project-text;
    }
  }

  &--cinemaddict {
    .card__inside {
      background-color: $cinemaddict-project;
      color: $code;

      strong {
        color: $cinemaddict-project-strong;
      }
    }
  }

  &--keksobooking {
    .card__inside {
      background-color: $keksobooking-project;
      color: $keksobooking-project-text;
    }
  }

  &--kekstagram {
    .card__inside {
      background-color: $kekstagram-project;
      color: $code;

      strong {
        color: $kekstagram-project-strong;
      }
    }
  }

  &--pognali {
    .card__inside {
      background-color: $pognali-project;
      color: $express;

      strong {
        color: $pognali-project-strong;
      }
    }
  }

  &--gllacy {
    .card__inside {
      background-color: $gllacy-project;
      color: $code;

      strong {
        color: $gllacy-project-strong;
      }
    }
  }

  &--nerds {
    .card__inside {
      background-color: $nerds-project;
      color: $nerds-project-text;

      strong {
        color: $nerds-project-strong;
      }
    }
  }

  &:hover {
    width: 900px;
    box-shadow:
    -.1rem 1.7rem 6.6rem -3.2rem rgba(0,0,0,0.75);

    .card__container {
      transform: rotateY(-180deg);
    }

    .card__inside {
      box-shadow: inset 1rem 0px 5rem -2.5rem rgba(0,0,0,0.1);
    }
  }
}

.card__flip {
  position: absolute;
  right: 0;
  width: 450px;
  height: 300px;
  transition: all 1s ease;
  visibility: hidden;
  perspective: 100rem;
  z-index: 100;
}

.card__flip > * {
  visibility: visible;
}

.card__container {
  position: absolute;
  right: 0;
  width: 100%;
  height: 100%;
  transform-origin: left;
  transform-style: preserve-3d;
  transition: all 1s ease;
}

.card__front,
.card__back {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
}

.card__back {
  transform: rotateY(180deg);
}

.card__inside {
  position: absolute;
  right: 0;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 450px;
  height: 300px;
  padding: 15px;
  box-shadow: inset 20rem 0px 5rem -2.5rem rgba(0,0,0,0.25);
  transition: all 1s ease;
  z-index: 1;
}

.card__activities {
  @include indents-none;
  @include list-reset;
  font-size: 14px;
  line-height: 20px;

  li {
    margin-bottom: 15px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  i {
    margin-right: 5px;
  }
}
