#code {
  padding-top: 15px;
  padding-left: 15px;
  font-family: monospace;
  font-size: 14px;
  line-height: 20px;
  color: $code;
}

.code__line {
  @include indents-none;
  overflow: hidden;
  white-space: nowrap;
  opacity: .9;
  animation:
    typing 0.6s steps(70, end);

  &:nth-child(4),
  &:nth-child(10) {
    margin-top: 10px;
  }
}

.code__function {
  color: $function;
}

.code__punctuation {
  color: $punctuation;
}

.code__keyword {
  color: $keyword;
}

.code__operator {
  color: $operator;
}

.code__string {
  color: $string;
}

.code__number {
  color: $number;
}

.code__object {
  color: $object;
}
