.skills__wrapper {
  @include flex;
  justify-content: center;
  align-items: flex-start;
  padding-top: 40px;
}

.skills__list {
  @include indents-none;
  @include list-reset;
  @include flex;
  justify-content: center;
  align-items: flex-start;
  width: 800px;
}

.skills__item {
  @include flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  width: 120px;
  height: 120px;
  margin-right: 30px;
  margin-bottom: 30px;
  padding: 5px;
  background-color: $skills-back;
  border-radius: 10px;

  transition: transform 0.5s;
  cursor: pointer;
  box-shadow:
  3px 3px 5px $skills-shadow,
  -2px -2px 5px $skills-shadow
  ;

  &:hover {
    transform: translateY(-3px);
    box-shadow:
    5px 5px 8px $skills-shadow,
    -4px -4px 8px $skills-shadow
    ;

    .skills__title {
      opacity: 1;
    }

    .devicon-javascript-plain {
      color: $javascript;
    }

    .devicon-nodejs-plain {
      color: $node;
    }

    .devicon-express-original {
      color: $express;
    }

    .devicon-react-original {
      color: $react;
    }

    .devicon-redux-original {
      color: $redux;
    }

    .devicon-postgresql-plain {
      color: $postgresql;
    }

    .devicon-sequelize-plain {
      color: $postgresql;
    }

    .devicon-socketio-original {
      color: $express;
    }

    .devicon-bootstrap-plain {
      color: $bootstrap;
    }

    .devicon-jest-plain {
      color: $jest;
    }

    .devicon-html5-plain-wordmark {
      color: $html-5;
    }

    .devicon-css3-plain-wordmark {
      color: $css-3;
    }

    .devicon-sass-original {
      color: $sass;
    }

    .devicon-less-plain-wordmark {
      color: $less;
    }

    .devicon-npm-original-wordmark {
      color: $npm;
    }

    .devicon-git-plain-wordmark {
      color: $git;
    }

    .devicon-webpack-plain {
      color: $webpack;
    }

    .devicon-gulp-plain {
      color: $gulp;
    }

    .devicon-babel-plain {
      color: $babel;
    }

    .iconify path {
      color: $pug;
    }
  }
}

.skills__title {
  @include indents-none;
  margin-top: 15px;
  opacity: 0.7;
  transition: opacity 0.5s;
}

.skills__item i,
.iconify path {
  text-align: center;
  font-size: 70px;
  transition: color 0.5s;
}

.skills__item svg {
  width: 70px;
  height: auto;
}

.skills__item-title {
  font-size: 24px;
  text-shadow: 2px 2px 5px $menu-button;
}

.skills__descriptions {
  @include indents-none;
  @include list-reset;
  line-height: 30px;
}

.skills__descriptions i {
  margin-right: 10px;
  animation:
  rotate-gear 2s steps(50) infinite;
}
