html {
  scroll-behavior: smooth;
}

body {
  box-sizing: border-box;
  font-family: "Source Code Pro", sans-serif;
  font-size: 18px;
  color: rgba(205, 241, 205, 0.8);
  background-color: #001100;
}

#background {
  margin: 0;
  padding: 0;
  position: fixed;
  top: 0;
  left: 0;
  z-index: -10;
  opacity: 0.3;
}

.container {
  box-sizing: border-box;
  width: 1400px;
  margin: 0 auto;
  padding-left: 40px;
  padding-right: 40px;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

/* Fade-In effect */
@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
/* The typing effect */
@keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 480px;
  }
}
/* The typewriter cursor effect */
@keyframes blink-caret {
  from, to {
    border-color: transparent;
  }
  50% {
    border-color: rgba(0, 255, 0, 0.8);
  }
}
/* Display flicker effect */
@keyframes flicker-display {
  from, to {
    filter: drop-shadow(0 -5px 5px rgba(0, 255, 0, 0.3));
  }
  50% {
    filter: drop-shadow(0 -5px 15px rgba(0, 255, 0, 0.5));
  }
}
/* Loader effect */
@keyframes loader-spinner {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
/* Gear effext */
@keyframes rotate-gear {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.button {
  color: inherit;
  text-decoration: none;
  font: inherit;
  background: transparent;
  border: none;
  position: relative;
  padding: 1rem 2rem;
  overflow: hidden;
  font-size: 16px;
  text-align: center;
  text-transform: uppercase;
  color: rgba(85, 170, 85, 0.8);
  border: 1px solid rgba(85, 170, 85, 0.9);
}
.button::before {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(120deg, transparent, rgba(85, 170, 85, 0.4), transparent);
  transition: all 650ms;
}
.button:hover, .button:focus {
  box-shadow: 1px 1px 25px 10px rgba(85, 170, 85, 0.4);
}
.button:hover::before, .button:focus::before {
  left: 100%;
}
.button:active {
  color: rgba(85, 170, 85, 0.5);
  text-shadow: rgba(85, 170, 85, 0.4);
  border: 1px solid rgba(85, 170, 85, 0.6);
}
.button--menu {
  font-size: 20px;
  color: rgba(136, 221, 136, 0.7);
  text-shadow: 2px 4px 5px rgba(136, 221, 136, 0.6);
  background-color: rgba(51, 85, 51, 0.2);
  border: 2px solid rgba(136, 221, 136, 0.8);
}
.button--menu::before {
  background: linear-gradient(120deg, transparent, rgba(136, 221, 136, 0.4), transparent);
}
.button--menu:hover, .button--menu:focus {
  box-shadow: 1px 1px 20px 10px rgba(136, 221, 136, 0.4);
}
.button--menu:active {
  color: rgba(136, 221, 136, 0.4);
  text-shadow: 2px 4px 5px rgba(136, 221, 136, 0.3);
  border: 2px solid rgba(136, 221, 136, 0.5);
}
.button--demo, .button--details {
  padding: 0.5rem 1rem;
}
.button--git {
  padding: 0.3rem;
  font-size: 22px;
}
.button--details {
  color: rgba(205, 241, 205, 0.8);
  border-color: rgba(205, 241, 205, 0.9);
}
.button--details::before {
  background: linear-gradient(120deg, transparent, rgba(205, 241, 205, 0.3), transparent);
}
.button--details:hover, .button--details:focus {
  box-shadow: 1px 1px 25px 10px rgba(205, 241, 205, 0.3);
}
.button--details:active {
  color: rgba(205, 241, 205, 0.6);
  text-shadow: rgba(205, 241, 205, 0.3);
  border: 1px solid rgba(205, 241, 205, 0.6);
}

.header {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-end;
  padding-bottom: 60px;
  padding-top: 50px;
}

.header__title-wrapper {
  width: 480px;
}

.header__title {
  margin: 0;
  padding: 0;
  overflow: hidden;
  font-family: "Press Start 2P", sans-serif;
  white-space: nowrap;
  letter-spacing: 1px;
  color: rgba(136, 221, 136, 0.8);
  text-shadow: 3px 4px 4px rgba(136, 221, 136, 0.5);
  border-right: 15px solid rgba(0, 255, 0, 0.8);
  animation: typing 1.2s steps(40, end), blink-caret 0.7s step-start infinite;
}

.header__navigation {
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  animation: fade-in 2s;
}

.header__navigation-item {
  margin: 0 20px;
}

.greeting {
  box-sizing: border-box;
  margin-bottom: 15px;
  padding-top: 80px;
  padding-bottom: 20px;
  min-height: 500px;
}

.greeting__sections {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  box-sizing: border-box;
  justify-content: flex-start;
  align-items: flex-start;
}

.greeting__sections-item {
  box-sizing: border-box;
  width: 380px;
  min-height: 400px;
}

.message {
  padding-top: 70px;
  padding-left: 20px;
}
.message--start {
  width: 300px;
}

.message__text {
  margin: 0;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  font-size: 20px;
  line-height: 28px;
  animation: typing 0.8s steps(70, end);
}
.message__text:nth-child(3), .message__text:nth-child(6) {
  margin-top: 40px;
}

.message__text span {
  font-weight: 700;
  color: rgba(0, 255, 0, 0.6);
}

.message__link {
  color: inherit;
  text-decoration: none;
  color: rgba(136, 221, 136, 0.8);
  text-shadow: 3px 3px 5px rgba(136, 221, 136, 0.5);
}
.message__link:hover, .message__link:focus, .message__link:active {
  color: rgba(136, 221, 136, 0.5);
}

.message__repeat {
  margin-top: 50px;
  margin-left: 75px;
  animation: fade-in 2s;
  cursor: pointer;
}

.computer {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-start;
  position: relative;
  width: 580px;
}

.computer__wrapper {
  box-sizing: border-box;
  position: relative;
  animation: fade-in 2s;
}

.computer__image {
  display: block;
  animation: flicker-display 0.1s step-start infinite;
}

.computer__display {
  box-sizing: border-box;
  position: absolute;
  top: 5px;
  left: 60px;
  width: 460px;
  height: 300px;
  border-radius: 10px;
}

#display {
  margin: 0;
  padding: 0;
  left: 70px;
}

.computer__link {
  color: inherit;
  text-decoration: none;
  margin-top: 50px;
}

.computer__link i {
  font-size: 28px;
  color: rgba(0, 255, 0, 0.6);
}

#code {
  padding-top: 15px;
  padding-left: 15px;
  font-family: monospace;
  font-size: 14px;
  line-height: 20px;
  color: #ffffff;
}

.code__line {
  margin: 0;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  opacity: 0.9;
  animation: typing 0.6s steps(70, end);
}
.code__line:nth-child(4), .code__line:nth-child(10) {
  margin-top: 10px;
}

.code__function {
  color: #79B6F2;
}

.code__punctuation {
  color: #88C6BE;
}

.code__keyword {
  color: #C5A5C5;
}

.code__operator {
  color: #D7DEEA;
}

.code__string {
  color: #8DC891;
}

.code__number {
  color: #5A9BCF;
}

.code__object {
  color: #FAC863;
}

#loader {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  animation: fade-in 5s;
}

.loader-spinner {
  position: relative;
  display: inline-block;
  vertical-align: center;
  width: 80px;
  height: 80px;
}

.loader-spinner__item {
  transform-origin: 40px 40px;
  animation: loader-spinner 1.2s linear infinite;
}
.loader-spinner__item::after {
  content: "";
  display: block;
  position: absolute;
  top: 3px;
  left: 37px;
  width: 6px;
  height: 18px;
  border-radius: 20%;
  background: #55AA55;
}
.loader-spinner__item:nth-child(1) {
  transform: rotate(0deg);
  animation-delay: -1.1s;
}
.loader-spinner__item:nth-child(2) {
  transform: rotate(30deg);
  animation-delay: -1s;
}
.loader-spinner__item:nth-child(3) {
  transform: rotate(60deg);
  animation-delay: -0.9s;
}
.loader-spinner__item:nth-child(4) {
  transform: rotate(90deg);
  animation-delay: -0.8s;
}
.loader-spinner__item:nth-child(5) {
  transform: rotate(120deg);
  animation-delay: -0.7s;
}
.loader-spinner__item:nth-child(6) {
  transform: rotate(150deg);
  animation-delay: -0.6s;
}
.loader-spinner__item:nth-child(7) {
  transform: rotate(180deg);
  animation-delay: -0.5s;
}
.loader-spinner__item:nth-child(8) {
  transform: rotate(210deg);
  animation-delay: -0.4s;
}
.loader-spinner__item:nth-child(9) {
  transform: rotate(240deg);
  animation-delay: -0.3s;
}
.loader-spinner__item:nth-child(10) {
  transform: rotate(270deg);
  animation-delay: -0.2s;
}
.loader-spinner__item:nth-child(11) {
  transform: rotate(300deg);
  animation-delay: -0.1s;
}
.loader-spinner__item:nth-child(12) {
  transform: rotate(330deg);
  animation-delay: 0s;
}

