.card {
  position: relative;
  width: 450px;
  height: 300px;
  font-family: "Arial", sans-serif;
  transition: all 1s ease;
  box-shadow: -0.1rem 1.7rem 6.6rem -3.2rem rgba(0, 0, 0, 0.5);
  opacity: 0.9;
}
.card--typoteka .card__inside {
  background-color: #bfdcf7;
  color: #000000;
}
.card--avtomoto .card__inside {
  background-color: #F5F5F9;
  color: #48494D;
}
.card--avtomoto .card__inside strong {
  color: #D12136;
}
.card--ligabank .card__inside, .card--ligabankcredit .card__inside {
  background-color: #F6F7FF;
  color: #1F1E25;
}
.card--ligabank .card__inside strong, .card--ligabankcredit .card__inside strong {
  color: #2C36F2;
}
.card--wtw .card__inside {
  background-color: #e1b0b2;
  color: #252525;
}
.card--cinemaddict .card__inside {
  background-color: #121213;
  color: #ffffff;
}
.card--cinemaddict .card__inside strong {
  color: #ffe800;
}
.card--keksobooking .card__inside {
  background-color: #f0f0ea;
  color: #353535;
}
.card--kekstagram .card__inside {
  background-color: #232321;
  color: #ffffff;
}
.card--kekstagram .card__inside strong {
  color: #ff4e4e;
}
.card--pognali .card__inside {
  background-color: #edeff6;
  color: #444444;
}
.card--pognali .card__inside strong {
  color: #1d2e5b;
}
.card--gllacy .card__inside {
  background-color: #849d8f;
  color: #ffffff;
}
.card--gllacy .card__inside strong {
  color: #ffbc9e;
}
.card--nerds .card__inside {
  background-color: #eeeeee;
  color: #000000;
}
.card--nerds .card__inside strong {
  color: #fb565a;
}
.card:hover {
  width: 900px;
  box-shadow: -0.1rem 1.7rem 6.6rem -3.2rem rgba(0, 0, 0, 0.75);
}
.card:hover .card__container {
  transform: rotateY(-180deg);
}
.card:hover .card__inside {
  box-shadow: inset 1rem 0px 5rem -2.5rem rgba(0, 0, 0, 0.1);
}

.card__flip {
  position: absolute;
  right: 0;
  width: 450px;
  height: 300px;
  transition: all 1s ease;
  visibility: hidden;
  perspective: 100rem;
  z-index: 100;
}

.card__flip > * {
  visibility: visible;
}

.card__container {
  position: absolute;
  right: 0;
  width: 100%;
  height: 100%;
  transform-origin: left;
  transform-style: preserve-3d;
  transition: all 1s ease;
}

.card__front,
.card__back {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
}

.card__back {
  transform: rotateY(180deg);
}

.card__inside {
  position: absolute;
  right: 0;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 450px;
  height: 300px;
  padding: 15px;
  box-shadow: inset 20rem 0px 5rem -2.5rem rgba(0, 0, 0, 0.25);
  transition: all 1s ease;
  z-index: 1;
}

.card__activities {
  margin: 0;
  padding: 0;
  list-style: none;
  font-size: 14px;
  line-height: 20px;
}
.card__activities li {
  margin-bottom: 15px;
}
.card__activities li:last-child {
  margin-bottom: 0;
}
.card__activities i {
  margin-right: 5px;
}

.about__items {
  display: grid;
  grid-template-columns: 440px 600px;
  grid-template-rows: 320px 320px;
  gap: 30px 70px;
  margin-top: 60px;
  padding: 0 70px;
}

.about__item {
  padding: 25px 30px;
  background-color: rgba(85, 170, 85, 0.1);
  border-radius: 10px;
  box-shadow: 3px 3px 5px rgba(136, 221, 136, 0.2), -2px -2px 5px rgba(136, 221, 136, 0.2);
}
.about__item--bio {
  grid-row-start: 1;
  grid-row-end: 3;
}
.about__item--work {
  grid-row-start: 1;
}
.about__item--education {
  grid-row-start: 2;
}

.about__photo-wrapper {
  position: relative;
  width: 300px;
  margin-left: 20px;
  margin-bottom: 40px;
  box-shadow: -2px 0 30px rgba(136, 221, 136, 0.3);
}
.about__photo-wrapper::before {
  content: "";
  position: absolute;
  top: 7px;
  left: 40px;
  width: 300px;
  height: 400px;
  background-color: rgba(136, 221, 136, 0.8);
  transform: rotate(3deg);
  opacity: 0.6;
}

.about__photo {
  display: block;
  opacity: 0.9;
}

.about__text {
  margin: 0;
  padding: 0;
  margin-bottom: 10px;
}
.about__text--dates {
  font-size: 16px;
  font-weight: 700;
  color: rgba(0, 255, 0, 0.5);
}

.about__title {
  font-weight: 700;
  font-size: 22px;
  text-transform: uppercase;
  color: rgba(85, 170, 85, 0.9);
  text-shadow: 2px 2px 5px rgba(85, 170, 85, 0.6);
}

.about__interests {
  font-size: 28px;
}

.about__interests i {
  position: relative;
}

.about__tooltip {
  position: absolute;
  top: 100%;
  left: 50%;
  opacity: 0;
  padding: 10px;
  font-family: "Source Code Pro", sans-serif;
  font-weight: normal;
  font-size: 18px;
  color: rgba(205, 241, 205, 0.8);
  text-align: center;
  white-space: nowrap;
  background-color: rgba(85, 170, 85, 0.5);
  border-radius: 5px;
  z-index: -1;
  transform: translateX(-50%);
}
.about__tooltip::before {
  content: "";
  position: absolute;
  bottom: 100%;
  left: 50%;
  margin-left: -8px;
  border: 8px solid rgba(85, 170, 85, 0.5);
  border-top-color: transparent;
  border-right-color: transparent;
  border-left-color: transparent;
}

.about__interests i:hover .about__tooltip,
.about__interests i:focus .about__tooltip {
  opacity: 1;
  z-index: 10;
  top: 150%;
  transition: 0.5s;
}

.about__section-title {
  margin: 0;
  padding: 0;
  font-size: 28px;
  text-transform: uppercase;
  color: rgba(136, 221, 136, 0.6);
  text-shadow: 2px 4px 5px rgba(136, 221, 136, 0.4);
}

.about__section-title i {
  color: rgba(205, 241, 205, 0.8);
  text-shadow: none;
}

.about__text-section {
  margin-top: 25px;
}

.footer {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;
  padding-bottom: 40px;
  padding-top: 50px;
  opacity: 0;
  transition: opacity 0.8s;
}

.footer__logo {
  margin: 0;
  padding: 0;
  margin-bottom: 10px;
  text-align: center;
}

.footer__logo span {
  margin: 0 5px;
  color: rgba(0, 255, 0, 0.8);
  font-weight: 700;
}

.footer__social {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.footer__social-link {
  display: inline-block;
  width: 25px;
  height: 25px;
  margin: 0 5px;
  text-align: center;
  vertical-align: middle;
  opacity: 0.9;
}

.footer__social-link a {
  color: inherit;
  text-decoration: none;
  color: rgba(0, 255, 0, 0.6);
  font-size: 26px;
  transition: all 0.5s;
}
.footer__social-link a:hover, .footer__social-link a:focus {
  color: rgba(0, 255, 0, 0.8);
  opacity: 1;
  font-size: 28px;
}
.footer__social-link a:active {
  opacity: 0.5;
}

.footer__social-link:nth-child(3) a {
  font-size: 28px;
}
.footer__social-link:nth-child(3) a:hover, .footer__social-link:nth-child(3) a:focus {
  font-size: 30px;
}

.footer__copyright {
  padding-left: 70px;
}

.footer__copyright-text {
  margin: 0;
  padding: 0;
  font-size: 20px;
  text-align: center;
}
.footer__copyright-text--date {
  margin-top: 15px;
  font-size: 18px;
  opacity: 0.8;
}

.footer__copyright-name {
  color: rgba(0, 255, 0, 0.6);
  font-weight: 700;
}

.footer__plates {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  margin-top: 15px;
  justify-content: center;
  align-items: center;
  font-size: 24px;
}

.footer__plates li {
  margin: 0 5px;
}

.footer__plates i:hover {
  color: inherit;
}