.about__items {
  display: grid;
  grid-template-columns: 440px 600px;
  grid-template-rows: 320px 320px;
  gap: 30px 70px;
  margin-top: 60px;
  padding: 0 70px;
}

.about__item {
  padding: 25px 30px;
  background-color: $skills-back;
  border-radius: 10px;
  box-shadow:
    3px 3px 5px $skills-shadow,
    -2px -2px 5px $skills-shadow
    ;

  &--bio {
    grid-row-start: 1;
    grid-row-end: 3;
  }

  &--work {
    grid-row-start: 1;
  }

  &--education {
    grid-row-start: 2;
  }
}

.about__photo-wrapper {
  position: relative;
  width: 300px;
  margin-left: 20px;
  margin-bottom: 40px;
  box-shadow: -2px 0 30px $menu-button-active;

  &::before {
    content: '';
    position: absolute;
    top: 7px;
    left: 40px;
    width: 300px;
    height: 400px;
    background-color: $title;
    transform: rotate(3deg);
    opacity: .6;
  }
}

.about__photo {
  display: block;
  opacity: .9;
}

.about__text {
  @include indents-none;
  margin-bottom: 10px;

  &--dates {
    font-size: 16px;
    font-weight: 700;
    color: $flick-bright;
  }
}

.about__title {
  font-weight: 700;
  font-size: 22px;
  text-transform: uppercase;
  color: $button-border;
  text-shadow: 2px 2px 5px $button-border-active;
}

.about__interests {
  font-size: 28px;
}

.about__interests i {
  position: relative;
}

.about__tooltip {
  position: absolute;
  top: 100%;
  left: 50%;
  opacity: 0;
  padding: 10px;
  font-family: $main-font;
  font-weight: normal;
  font-size: 18px;
  color: $main-text;
  text-align: center;
  white-space: nowrap;
  background-color: $button-active;
  border-radius: 5px;
  z-index: -1;
  transform: translateX(-50%);


  &::before {
    content: "";
    position: absolute;
    bottom: 100%;
    left: 50%;
    margin-left: -8px;
    border: 8px solid $button-active;
    border-top-color: transparent;
    border-right-color: transparent;
    border-left-color: transparent;
  }
}

.about__interests i:hover .about__tooltip,
.about__interests i:focus .about__tooltip {
    opacity: 1;
    z-index: 10;
    top: 150%;
    transition: 0.5s;
}

.about__section-title {
  @include indents-none;
  font-size: 28px;
  text-transform: uppercase;
  color: $menu-button-text-shadow;
  text-shadow: 2px 4px 5px $menu-button-shadow;
}

.about__section-title i {
  color: $main-text;
  text-shadow: none;
}

.about__text-section {
  margin-top: 25px;
}
