.computer {
  @include flex;
  justify-content: center;
  align-items: flex-start;
  position: relative;
  width: 580px;
}

.computer__wrapper {
  box-sizing: border-box;
  position: relative;
  animation:
  fade-in 2s;
}

.computer__image {
  display: block;
  animation:
  flicker-display .1s step-start infinite;
}

.computer__display {
  box-sizing: border-box;
  position: absolute;
  top: 5px;
  left: 60px;
  width: 460px;
  height: 300px;
  border-radius: 10px;
}

#display {
  @include indents-none;
  left: 70px;
}

.computer__link {
  @include link-reset;
  margin-top: 50px;

}

.computer__link i {
  font-size: 28px;
  color: $message-bold;
}

