.social {
  @include indents-none;
  @include list-reset;
  @include flex;
  justify-content: flex-start;
  align-items: center;
  margin-top: 85px;
  margin-left: 40px;
  animation:
  fade-in 2s;
}

.social__link {
  width: 30px;
  height: 30px;
  margin: 0 7px;
  text-align: center;

  &--tag {
    @include flex;
    justify-content: center;
    align-items: center;
    width: 20px;
    margin: 0;
    padding-top: 10px;

    &:last-child {
      margin-left: 7px;
    }
  }
}

.social__link a {
  @include link-reset;
  color: $message-bold;
  font-size: 34px;
  opacity: 0.7;
  transition: all .5s;

  &:hover,
  &:focus {
    color: $cursor;
    opacity: 1;
    font-size: 36px;
  }

  &:active {
    opacity: 0.5;
  }
}

.social__link span {
  color: $cursor;
  font-size: 24px;
}
