.footer {
  @include flex;
  justify-content: space-between;
  align-items: flex-start;
  padding-bottom: 40px;
  padding-top: 50px;
  opacity: 0;
  transition: opacity .8s;
}

.footer__logo {
  @include indents-none;
  margin-bottom: 10px;
  text-align: center;
}

.footer__logo span {
  margin: 0 5px;
  color: $cursor;
  font-weight: 700;
}

.footer__social {
  @include indents-none;
  @include list-reset;
  @include flex;
  justify-content: center;
  align-items: center;
}

.footer__social-link {
  display: inline-block;
  width: 25px;
  height: 25px;
  margin: 0 5px;
  text-align: center;
  vertical-align: middle;
  opacity: 0.9;
}

.footer__social-link a {
  @include link-reset;
  color: $message-bold;
  font-size: 26px;
  transition: all .5s;

  &:hover,
  &:focus {
    color: $cursor;
    opacity: 1;
    font-size: 28px;
  }

  &:active {
    opacity: 0.5;
  }
}

.footer__social-link:nth-child(3) a {
    font-size: 28px;

    &:hover,
    &:focus {
      font-size: 30px;
    }
}

.footer__copyright {
  padding-left: 70px;
}

.footer__copyright-text {
  @include indents-none;
  font-size: 20px;
  text-align: center;

  &--date {
    margin-top: 15px;
    font-size: 18px;
    opacity: 0.8;
  }
}

.footer__copyright-name {
  color: $message-bold;
  font-weight: 700;
}

.footer__plates {
  @include indents-none;
  @include list-reset;
  @include flex;
  margin-top: 15px;
  justify-content: center;
  align-items: center;
  font-size: 24px;
}

.footer__plates li {
  margin: 0 5px;
}

.footer__plates i:hover {
  color: inherit;
}
