.message {
  padding-top: 70px;
  padding-left: 20px;

  &--start {
    width: 300px;
  }
}

.message__text {
  @include indents-none;
  overflow: hidden;
  white-space: nowrap;
  font-size: 20px;
  line-height: 28px;
  animation:
    typing 0.8s steps(70, end);

  &:nth-child(3),
  &:nth-child(6) {
    margin-top: 40px;
  }
}

.message__text span {
  font-weight: 700;
  color: $message-bold;
}

.message__link {
  @include link-reset;
  color: $title;
  text-shadow: 3px 3px 5px $title-shadow;

  &:hover,
  &:focus,
  &:active {
    color: $title-shadow;
  }
}

.message__repeat {
  margin-top: 50px;
  margin-left: 75px;
  animation:
  fade-in 2s;
  cursor: pointer;
}
