/* Fade-In effect */
@keyframes fade-in {
  from {opacity: 0}
  to {opacity: 1}
}

/* The typing effect */
@keyframes typing {
  from {width: 0}
  to {width: 480px}
}

/* The typewriter cursor effect */
@keyframes blink-caret {
  from, to {border-color: transparent}
  50% {border-color: $cursor}
}

/* Display flicker effect */
@keyframes flicker-display {
  from, to {filter: drop-shadow(0 -5px 5px $flick-dim)}
  50% {filter: drop-shadow(0 -5px 15px $flick-bright)}
}

/* Loader effect */
@keyframes loader-spinner {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

/* Gear effext */
@keyframes rotate-gear {
  from {transform: rotate(0deg)}
  to { transform: rotate(360deg)}
}
