.greeting {
  box-sizing: border-box;
  margin-bottom: 15px;
  padding-top: 80px;
  padding-bottom: 20px;
  min-height: 500px;
}

.greeting__sections {
  @include indents-none;
  @include list-reset;
  @include flex;
  box-sizing: border-box;
  justify-content: flex-start;
  align-items: flex-start;
}

.greeting__sections-item {
  box-sizing: border-box;
  width: 380px;
  min-height: 400px;
}
