.header {
  @include flex;
  justify-content: space-between;
  align-items: flex-end;
  padding-bottom: 60px;
  padding-top: 50px;
}

.header__title-wrapper {
  width: 480px;
}

.header__title {
  @include indents-none;
  overflow: hidden;
  font-family: $title-font;
  white-space: nowrap;
  letter-spacing: 1px;
  color: $title;
  text-shadow: 3px 4px 4px $title-shadow;
  border-right: 15px solid $cursor;
  animation:
    typing 1.2s steps(40, end),
    blink-caret .7s step-start infinite;
}

.header__navigation {
  @include indents-none;
  @include flex;
  justify-content: space-between;
  animation: fade-in 2s;
}

.header__navigation-item {
  margin: 0 20px;
}
