.projects__list {
  @include indents-none;
  @include list-reset;
  @include flex;
  justify-content: center;
  align-items: flex-start;
  margin-top: 40px;
}

.projects__item {
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 50px;
  padding: 15px;
  min-width: 70%;
  background-color: $menu-button-back;
  box-shadow:
    3px 3px 5px $skills-shadow,
    -2px -2px 5px $skills-shadow
    ;

    &:last-child {
      margin-bottom: 0;
    }
}

.projects__info {
  max-width: 330px;
  margin-left: 25px;
}

.projects__title {
  @include indents-none;
  margin-bottom: 15px;
  font-size: 28px;
  text-transform: uppercase;
  color: $button;
  text-shadow: 2px 4px 5px $projects-text-shadow;
}

.projects__description {
  @include indents-none;
  line-height: 24px;
}


.projects__footer {
  @include flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 25px;
  padding-bottom: 5px;
}

.projects__footer-buttons {
  @include flex;
  justify-content: flex-start;
  align-items: flex-start;
}

.projects__footer-button {
  box-sizing: border-box;
  height: 37px;

  &:first-child {
    margin-right: 10px;
  }

  &:last-child {
    width: 37px;
  }
}

.projects__footer-plates {
  @include indents-none;
  @include list-reset;
  @include flex;
  justify-content: flex-end;
  align-items: center;
  font-size: 26px;

  li {
    @include flex;
    justify-content: center;
    align-items: center;
    margin: 0 5px;
  }

  i:hover {
    color: inherit;
  }

  .iconify path {
    fill: transparent;

    &:last-child {
      fill: $main-text;
    }
  }
}
