html {
  scroll-behavior: smooth;
}

body {
  box-sizing: border-box;
  font-family: $main-font;
  font-size: 18px;
  color: $main-text;
  background-color: $main-back;
}

#background {
  @include indents-none;
  position: fixed;
  top: 0;
  left: 0;
  z-index: -10;
  opacity: 0.3;
}

.container {
  box-sizing: border-box;
  width: 1400px;
  margin: 0 auto;
  padding-left: 40px;
  padding-right: 40px;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
