.section {
  box-sizing: border-box;
  padding-bottom: 50px;
  padding-top: 40px;
  min-height: 750px;
  opacity: 0;
  transition: opacity .8s;
}

.section__header {
  position: relative;
  margin-bottom: 20px;
  padding-bottom: 10px;
}

.section__title {
  @include indents-none;
  font-size: 28px;
  text-transform: uppercase;
  letter-spacing: 1px;
  color: $title;
  text-shadow: 2px 4px 5px $title-shadow;
}

.section__title span {
  padding-left: 20px;
  font-weight: 500;
  text-shadow: 1px 3px 4px $title-shadow;
  border-left: 5px solid $cursor;
}
